eros/hero-image_2.jpg);\r\n*/\r\n\r\n.hero img {\r\n  width: 100%;\r\n  height: 80%;\r\n  background-size: cover;\r\n  object-fit: cover;\r\n}\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nmain {\r\n  /* padding: 32px 0; */\r\n  flex: 1;\r\n}\r\n\r\n.resto-item__not__found {\r\n  text-align: center;\r\n  margin: 20px;\r\n}\r\n\r\n.content {\r\n  padding: 50px auto;\r\n  margin: 0 0;\r\n  width: 100%;\r\n  min-height: 100%;\r\n}\r\n\r\n.heading {\r\n  margin: 15px auto;\r\n  text-align: center;\r\n  color: black;\r\n}\r\n\r\n.content .content__heading {\r\n  color: black;\r\n  margin: 20px auto;\r\n  font-weight: normal;\r\n  text-align: center;\r\n}\r\n\r\n.article-container {\r\n  display: grid;\r\n  margin: 0 20px;\r\n  grid-template-columns: 1fr;\r\n  gap: 1.5rem;\r\n}\r\n\r\n.resto-item__not__found {\r\n  text-align: left;\r\n}\r\n\r\n.article-item {\r\n  overflow: hidden;\r\n  border-radius: 0.5rem;\r\n  margin: 10px;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n  background: #fff;\r\n}\r\n\r\n.article-item img {\r\n  height: 300px;\r\n  width: 100%;\r\n  object-fit: cover;\r\n}\r\n\r\n.article-detail {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.article-detail span {\r\n  margin-right: 20px;\r\n}\r\n\r\n.article-content h3 a {\r\n  padding: 10px 0;\r\n}\r\n\r\n.link-restaurant {\r\n  padding: 5px;\r\n}\r\n\r\n.link-restaurant h3 {\r\n  font-size: 1.5em;\r\n}\r\n\r\n.article-content a {\r\n  text-decoration: none;\r\n  color: rgb(31, 31, 31);\r\n}\r\n.article-content a:hover {\r\n  text-decoration: none;\r\n  color: rgb(0, 0, 0);\r\n}\r\n\r\n.line-clamp {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4;\r\n  -webkit-box-orient: vertical;\r\n  text-align: justify;\r\n}\r\n\r\n.restaurant-box {\r\n  overflow: hidden;\r\n  border-radius: 0.5rem;\r\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n  background: #fff;\r\n  margin: 0 auto;\r\n}\r\n\r\n.restaurant-detail {\r\n  text-align: left;\r\n  margin: 20px;\r\n}\r\n\r\n.restaurant-detail h3 {\r\n  font-size: 30px;\r\n}\r\n\r\n.restaurant-detail span {\r\n  font-size: 18px;\r\n  display: block;\r\n  font-weight: bold;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.restaurant-detail p {\r\n  font-size: 14px;\r\n  line-height: 30px;\r\n  margin-bottom: 20px;\r\n  /* text-align: justify; */\r\n}\r\n\r\n.content-title {\r\n  text-align: left;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.content-title h5 {\r\n  font-weight: 600;\r\n  line-height: 21px;\r\n  text-transform: uppercase;\r\n  font-size: 18px;\r\n  margin: 20px;\r\n}\r\n\r\n.section-container {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n}\r\n\r\n.makanan .minuman {\r\n  width: 100%;\r\n  max-width: 200px;\r\n}\r\n\r\n.section {\r\n  width: 100%;\r\n  margin: 20px auto;\r\n}\r\n\r\n.section span {\r\n  display: inline-block;\r\n  font-size: 15px;\r\n  color: #000;\r\n  background: rgba(255, 0, 0, 0.151);\r\n  padding: 10px 20px;\r\n  border-radius: 4px;\r\n  margin: 2px;\r\n  transition: all, 0.3s;\r\n}\r\n\r\n.section ul {\r\n  list-style-type: none;\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  align-items: center;\r\n}\r\n\r\n.review-pic {\r\n  float: left;\r\n  margin-right: 20px;\r\n}\r\n\r\n.review-pic img {\r\n  height: 50px;\r\n  width: 50px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.review-text {\r\n  text-align: left;\r\n  max-width: 400px;\r\n  overflow: hidden;\r\n  background: rgba(0, 140, 255, 0.2);\r\n  padding: 18px 30px 16px 20px;\r\n  margin-right: 10px;\r\n  border-radius: 10px;\r\n}\r\n\r\n.review-text h6 {\r\n  color: #131313;\r\n  font-weight: 700;\r\n  margin-bottom: 10px;\r\n  font-size: 15px;\r\n}\r\n\r\n.review-text p {\r\n  line-height: 23px;\r\n  margin-bottom: 0;\r\n  font-size: 14px;\r\n}\r\n\r\n/* LIKE */\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n  position: